<template>
    <div class="main_block_item">
        <v-row 
        class="item"
        @touchstart="touchStart"
        @touchmove="touchMove" 
        @touchend="touchEnd" 
        :class="[isDelete ? 'show_btn' : 'disable_btn']"
        >
            <v-col cols="2"></v-col>
                <v-col cols="8">
                    <div class="">
                        <div class="value text-center"> {{ element.value }} </div>
                        <div class="text-center"> {{ element.meaning }} </div>
                    </div>
                </v-col>
                <v-col cols="2" class="d-flex align-center justify-center">
                    <v-btn color="#fff" text icon @click="sing(element.value)">
                        <v-icon>mdi-volume-high</v-icon>
                    </v-btn>
            </v-col>
        </v-row>

        <div 
        color="red" 
        class="delete_btn d-flex align-center justify-end"
        @click="deleteElement(index)"
        >
            <v-icon color="#fff" x-large class="mr-9">
                mdi-delete
            </v-icon>
        </div>
    </div>
    
</template>

<script>
export default {
    props: ['element', 'index', 'typeList', 'folderName'],

    data:() => ({
        tochStartValue: 0,
        moveLength: 0,
        currentIndex: 0,
        isDelete: false
    }),

    computed: {

    },

    methods: {
        touchStart(e) {
            this.tochStartValue = e.touches[0].pageX
            // console.log(this.tochStartValue)
        },

        touchMove(e) {
            this.moveLength = this.tochStartValue - e.touches [0].pageX
        },

        touchEnd(e) {
            if (this.moveLength > 60) {
                this.isDelete = true
                this.currentIndex = e.currentTarget.dataset.target;
            }
            if (this.moveLength < -60) {
                this.isDelete = false;
                this.currentIndex = null;
            }
            console.log(this.isDelete)
        },

        async deleteElement(index) {
            let data = {
                index: index,
                path: this.typeList,
                folderName: this.folderName
            }
            await this.$store.dispatch('deleteData', data)
            await this.$store.dispatch('getData', {path: this.typeList})
        },

        sing(value) {
            speechSynthesis.speak(new SpeechSynthesisUtterance(value))
        },
    },

    
}
</script>

