        background: #ff5252;
        border-radius: 25px 25px 25px 25px;
        z-index: 0;
    }

    .delete_btn span {
        color: #fff;
        text-transform: uppercase;
    }

    .show_btn {
        animation-name: move;
        animation-duration: .6s;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;
