import { getDatabase, ref, onValue, get, child, remove} from "firebase/database";
import { fb } from "@/firebase-config"

export default {
    state: {
        
    },

    getters: {
       
    },

    actions: {
        deleteData({dispatch, rootGetters}, data) {
            if(data.path === 'idioms') {
                dispatch('deleteEl', data)
            } else if((data.path === 'words')) {
                dispatch('deleteElFromFolder', data)
            }
        },

        async deleteEl(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                remove(child(dbRef, `idioms/${localStorage.getItem('uid')}/${data.index}`)).then((resp) => {
                    console.log(resp)
                });
            } catch (e) {
                console.log(e)
            }
        },

        async deleteElFromFolder(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                remove(child(dbRef, `${data.path}/${localStorage.getItem('uid')}/${data.folderName}/${data.index}`)).then((resp) => {
                    console.log(resp)
                });
            } catch (e) {
                console.log(e)
            }
        },

    },

    
    mutations: {
       
    },
}