<template>
    <div class="mt-15 main">
     <TopBar/>
        <div class="text-center adding_block">
            <button class="add_btn py-5 mt-5 mb-5" @click="dialog = !dialog">
                add new +
            </button>
        </div>

        <hr class="mb-7">

        <v-container class="container_margin">
                <Item 
                v-for="(i, index) in data" 
                :key="index" 
                :element="i" 
                :index="index" 
                :typeList="typeList"
                :folderName="folderName"/>
        </v-container>

        <!-- Dialog for adding new iteems START -->
        <v-dialog
            v-model="dialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
            overlay-color="#fff"
        >
            <div class="dialog_top_bar">
                <v-container class="d-flex align-center justify-end">
                    <v-btn text icon @click="closeDialod()">
                        <v-icon large>mdi-close</v-icon>
                    </v-btn>
                </v-container>
            </div>
            <div class="dialog d-flex align-center">
                <v-container>
                     <v-text-field
                        v-model="new_value"
                        :label="typeList"
                        outlined
                        clearable
                        color="#4ecac2"
                    ></v-text-field>
                     <v-text-field
                        v-model="new_meaning"
                        label="meaning"
                        outlined
                        clearable
                        color="#4ecac2"
                    ></v-text-field>
                    <div class="radio text-center">
                        <button class="submit_btn py-5 mt-3 mb-5" @click="addNew()">
                            add new {{ typeList }}
                        </button>
                    </div>
                </v-container>
            </div>
        </v-dialog>
         <!-- Dialog for adding new iteems FINISH -->
    </div>
</template>

<script>
import TopBar from '@/components/TopBar.vue'
import {mapGetters} from 'vuex'
import Item from '@/views/elements/ItemForList.vue'
export default {
    props: ['wordsList', 'typeList', 'folderName'],
    components: {
        TopBar,
        Item
    },
    data: () => ({
        dialog: false,
        new_value: '',
        new_meaning: '',
    }),

    computed: {
        ...mapGetters(['DATA', 'PATH']),
        data() {
            if(this.PATH === 'words' || this.PATH === 'phrases') {
                return this.DATA[this.folderName]
            } else {
                return this.DATA
            }
        },

        widthForLoader() {
            return window.innerWidth
        }
    },

    methods: {
      async addNew() {
        const data = {
            path: this.typeList,
            new_value: this.new_value,
            new_meaning: this.new_meaning,
            folderName: this.folderName,
        }
        await this.$store.dispatch('addNewData', data)
        await this.$store.dispatch('getData', {userId: this.$store.state.auth.uid, path: this.typeList})

        this.closeDialod()
      },

      closeDialod() {
        this.new_value = ''
        this.new_meaning = ''
        this.dialog = !this.dialog
      },

      startDrag(a) {
        console.log('work')
      },

      swipeHandler() {
        console.log('swipe_left')
      }
    }
    
}
</script>

