import { getDatabase, ref, onValue, get, child} from "firebase/database";
import { fb } from "@/firebase-config"

export default {
    state: {
        idioms: ''
    },

    getters: {
        DATA(state) {
            return state.idioms
        }
    },

    actions: {
        getData({dispatch, rootGetters}, data) {
            if(data.path === 'idioms') {
                dispatch('getIdioms', data)
            } else if((data.path === 'words')) {
                dispatch('getWords', data)
            } else if((data.path === 'phrases')) {
                dispatch('getPhrases', data)
            } else if((data.path === 'phrasal_verbs')) {
                dispatch('getPhrasalVerbs', data)
            } else if((data.path === 'rules')) {
                dispatch('getRules', data)
            }
        },

        async getIdioms(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                get(child(dbRef, `idioms/${localStorage.getItem('uid')}`)).then((snapshot) => {
                    if (snapshot.exists()) {
                        ctx.commit('setIdioms', snapshot.val())
                    } else {
                      console.log("No data available");
                    }
                });
            } catch (e) {
                console.log(e)
            }
        },

        async getWords(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                get(child(dbRef, `words/${localStorage.getItem('uid')}`)).then((snapshot) => {
                    if (snapshot.exists()) {
                        ctx.commit('setIdioms', snapshot.val())
                    } else {
                      console.log("No data available");
                    }
                });
            } catch (e) {
                console.log(e)
            }
        },

        async getPhrases(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                get(child(dbRef, `phrases/${localStorage.getItem('uid')}`)).then((snapshot) => {
                    if (snapshot.exists()) {
                        ctx.commit('setIdioms', snapshot.val())
                    } else {
                        ctx.commit('setIdioms', '')
                      console.log("No data available");
                    }
                });
            } catch (e) {
                console.log(e)
            }
        },

        async getPhrasalVerbs(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                get(child(dbRef, `phrasal_verbs/${localStorage.getItem('uid')}`)).then((snapshot) => {
                    if (snapshot.exists()) {
                        ctx.commit('setIdioms', snapshot.val())
                    } else {
                      console.log("No data available");
                    }
                });
            } catch (e) {
                console.log(e)
            }
        },

        async getRules(ctx, data) {
            try {
                const dbRef = ref(fb.database);
                get(child(dbRef, `rules/${localStorage.getItem('uid')}`)).then((snapshot) => {
                    if (snapshot.exists()) {
                        ctx.commit('setIdioms', snapshot.val())
                    } else {
                      console.log("No data available");
                    }
                });
            } catch (e) {
                console.log(e)
            }
        },

    },

    
    mutations: {
        setIdioms(state, info) {
            state.idioms = info
        }
    },
}