<template>
    <v-container class="mt-15">
        <v-btn @click="getRandomElements">go</v-btn>

        <div>
            <v-carousel hide-delimiters>
                <v-carousel-item
                v-for="(item,i) in list"
                :key="i"
                >
                    {{ item }}
                <div v-for="i in newList" :key="i.index">
                    {{ i }}
                </div>
                </v-carousel-item>
            </v-carousel>
        </div>
    </v-container>
</template>

<script>
export default {
    data:() => ({
        newList: null,
        list: [
            {
                word: "some",
                trans: '1'
            },
            {
                word: "some",
                trans: '2'
            },
            {
                word: "some",
                trans: '3'
            },
            {
                word: "some",
                trans: '4'
            },
            {
                word: "some",
                trans: '4'
            },
            {
                word: "some",
                trans: '5'
            },
            {
                word: "some",
                trans: '6'
            },
        ]
    }),

    methods: {
        getRandomElements(item) {
            this.newList = null
            let newArr = []
            newArr.push(item)

            let a = this.list.map(item => item)
            for(let i = 0; i < 3; i++) {
                let el = a[Math.floor(Math.random()*a.length)]
                if(newArr.includes(el.trans) === false) {
                    newArr.push(el.trans)
                }
            } 
            if(a.length < 4  ) {
                return this.newList = newArr
            }  else if(newArr.length === 4) {
                return this.newList = newArr
            } else {
                this.getRandomElements()
            }
        }
    }
}
</script>


