<template>
  <v-app>
    <v-main>
      <router-view class=""/>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  data: () => ({
    pathname: "",
  }),

  watch: {
    'window.location.pathname': function() {
      this.pathname = window.location.pathname
    },
  },

  mounted() {
    let detect = new MobileDetect(window.navigator.userAgent)
    if(detect.mobile() === null) {
      this.$router.push({name: 'ErrorPage'})
    }
  }
};

</script>

