<template>
    <div id="message" class="d-flex align-center justify-center" width="500">
        <Transition>
            <div v-show="show">
                {{ message }}
            </div>
        </Transition>
    </div>
</template>

<script>
import messages from '@/utils/message'
import {mapMutations} from 'vuex'

export default {
    data: () => ({
        show: false,
        message: null,
    }),
    computed: {
        error() {
            return this.$store.getters.error
        }
    },

    watch: {
        error(fbError) {
            if(fbError !== null) {
                this.showMessage(fbError)
            }
        }
    },

    methods: {
        ...mapMutations(['clearError']),
        showMessage(fbError) {
            this.message = (messages[fbError] || '   ')
            this.show = true
            setTimeout(() => {
                this.show = false
               this.clearError()
            }, 5000);
        },

        // showDialog() {
            
        // }
    }
    
}
</script>

<style scoped>
    #message {
        min-height: 40px;
        color: #ff6b36;
        font-size: 20px;
        margin-top: 5px;
    }

    /* we will explain what these classes do next! */
    .v-enter-active,
    .v-leave-active {
        transition: opacity 0.5s ease;
    }

    .v-enter-from,
    .v-leave-to {
        opacity: 0;
    }
</style>