<template>
    <div class="mt-15">
        <TopBar/>
        <div class="text-center">
            <button class="add_btn py-5 mt-5 mb-5" @click="dialog = !dialog">
                add new topic +
            </button>
        </div>

        <hr class="mb-7">

        <v-container>
            <v-row v-for="(i, index) in DATA" :key="index" class="item mb-3 ma-auto">
                <v-col cols="2"></v-col>
                <v-col cols="8">
                    <div class="">
                        <div @click="goToTheFolder(i, index)" class="value text-center"> {{ index }} </div>
                    </div>
                </v-col>
                <v-col cols="2" class="d-flex align-center justify-center">
                </v-col>
            </v-row>
        </v-container>
        
        <v-dialog
            v-model="dialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
            overlay-color="#fff"
        >
            <div class="dialog_top_bar">
                <v-container class="d-flex align-center justify-end">
                    <v-btn text icon @click="closeDialod()">
                        <v-icon large>mdi-close</v-icon>
                    </v-btn>
                </v-container>
            </div>
            <div class="dialog d-flex align-center">
                <v-container>
                     <v-text-field
                        v-model="newFolderName"
                        label="Think of a name for the folder"
                        outlined
                        clearable
                        color="#4ecac2"
                    ></v-text-field>
                    <v-text-field
                        v-model="word"
                        label="First word"
                        outlined
                        clearable
                        color="#4ecac2"
                    ></v-text-field>
                    <v-text-field
                        v-model="meaning"
                        label="Meaning"
                        outlined
                        clearable
                        color="#4ecac2"
                    ></v-text-field>
                    <div class="radio text-center">
                        <button class="submit_btn py-5 mt-3 mb-5" @click="addNewFolder()">
                            add new folder
                        </button>
                    </div>
                </v-container>
            </div>
        </v-dialog>
    </div>
</template>

<script>
import TopBar from '@/components/TopBar.vue'
import { mapGetters } from 'vuex'
export default {
    components: {
        TopBar
    },
    props: ['typeList'],
    data: () => ({
        dialog: false,
        newFolderName: '',
        word: '',
        meaning: '',

    }),

    computed: {
        ...mapGetters(['DATA', 'PATH']),
    },

    methods: {
      async addNewFolder() {
        const data = {
            newFolderName: this.newFolderName,
            word: this.word,
            meaning: this.meaning,
            path: this.typeList
        }
        await this.$store.dispatch('addNewTopics', data)
        this.closeDialod()
      },

      sing(value) {
        speechSynthesis.speak(new SpeechSynthesisUtterance(value))
      },

      goToTheFolder(val, folderName) {
        console.log(val, folderName)
        
        this.$router.push({name: 'List', params: {wordsList: val, typeList: this.typeList, folderName: folderName}})
      },

      closeDialod() {
        this.newFolderName = ''
        this.word = ''
        this.meaning = ''
        this.dialog = !this.dialog
        console.log(this.typeList)
        this.$store.dispatch('getData', {path: this.typeList})
      }
    }
    
}
</script>

