import Vue from 'vue'
import VueRouter from 'vue-router'
import MainPage from '../views/MainPage.vue'
import List from '../views/List.vue'
import Topics from '../views/Topics.vue'
import Phrases from '../views/Phrases.vue'
import Auth from '../components/Auth.vue'
import ErrorPage from '../components/ErrorPage.vue'
import MainPageGames from '../views/games/MainPageGames.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Auth',
    component: Auth
  },
  {
    path: '/main_page',
    name: 'MainPage',
    component: MainPage,
    props: true
  },
  {
    path: '/list',
    name: 'List',
    component: List,
    props: true
  },
  {
    path: '/topics',
    name: 'Topics',
    component: Topics,
    props: true
  },
  {
    path: '/phrases_folders',
    name: 'Phrases',
    component: Phrases,
    props: true
  },
  {
    path: '/error_page',
    name: 'ErrorPage',
    component: ErrorPage,
    props: true
  },
  {
    path: '/memorisation',
    name: 'MainPageGames',
    component: MainPageGames,
    props: true
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
