import Vue from 'vue'
import Vuex from 'vuex'
import auth from './modules/auth'
import addNewInfo from './modules/addNewInfo'
import addNewTopics from './modules/addNewTopic'
import getData from './modules/getData'
import deleteInfo from '@/store/modules/deleteInfo'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    error: null,
  },

  getters: {
    error(state) {
      return state.error
    }
  },

  mutations: {
    setError(state, error) {
      state.error = error
    },
    clearError(state) {
      state.error = null
    }
  },

  actions: {
  },
  modules: {
    auth,
    addNewInfo,
    getData,
    addNewTopics,
    deleteInfo
  }
})
