import { getDatabase, ref, set, push } from "firebase/database";
import { fb } from "@/firebase-config"

export default {
    state: {
        
    },

    actions: {
       async addNewTopics(ctx, data) {
        try {
            let folderName = `${data.newFolderName}/`
            const postListRef = ref(fb.database, `${data.path}/${localStorage.getItem('uid')}/` + folderName );
            const newFolder = push(postListRef);
            set(newFolder, {value: data.word, meaning: data.meaning});
          } catch (e) {
            console.error("Error adding document: ", e);
          }
       },
    }
}